.navbar {
    background-color: $bgnavbar;

    .navbar-brand {
        color: $navlogo;
    }
    .navbar-nav {
        padding-right: 100px;
        .nav-item {
            .nav-link {
                color: $navlogo;
                font-weight: bold;
                padding-left: 20px;
                padding-right: 20px;
                &:hover {
                    background-color: $btnYellow;
                    border-radius: 2px;
                }
            }
        }
    }
    .search {
        .btn1 {
            background-color: $btnYellow; /* Green */
            border: none;
            color: $black;
            text-align: center;
            text-decoration: none;
            display: inline-block;
            font-size: 16px;
            margin-right: 20px;
            border-radius: 4px;

            width: 100px;

            cursor: pointer;
            &:hover {
                background-color: $black;
                color: $whitebtn;
            }
        }

        .has-search .form-control {
            padding-left: 1.375rem;
            margin-top: 3px;
            width: 44px;
            height: 44px;
        }

        .has-search .form-control-feedback {
            position: absolute;
            z-index: 2;
            display: block;
            width: 2.375rem;
            height: 2.375rem;
            line-height: 2.375rem;
            text-align: center;
            pointer-events: none;
            color: $black;
        }
    }
}
